.checkbox {
  position: relative;
  width: 20px;
  height: 20px; }

.checkbox input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.checkbox label {
  position: absolute;
  width: 20px;
  height: 20px;
  cursor: pointer; }

.checkbox label::before {
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  left: 0;
  top: 0;
  background-color: #ffffff;
  border: 2px solid #b3b3b3;
